.wrapper {
  height: 100%;
  grid-template: "header" 72px
                 "main" 1fr
                 / 1fr;
  display: grid;
}

.wrapper__header {
  grid-area: header;
}

.main {
  grid-area: main;
  margin-top: 40px;
}

.wrapper__sidebar {
  grid-area: sidebar;
}

.wrapper__content {
  grid-area: content;
}

@media (min-width: 768px) {
  .wrapper {
    height: 100%;
    grid-template: "header" 72px
                   "main" 1fr
                   / 1fr;
    display: grid;
  }

  .main {
    grid-template: "sidebar" 472px
                   "content" 1fr
                   / 1fr;
    row-gap: 100px;
    display: grid;
  }

  .wrapper__sidebar {
    grid-template-rows: 472px;
    grid-template-columns: 1fr 1fr;
    display: grid;
  }
}

@media (min-width: 1440px) {
  .wrapper {
    height: 100%;
    grid-template: "header" 72px
                   "main" 1fr
                   / 1fr;
    display: grid;
  }

  .main {
    grid-template: "sidebar content" 1fr
    / 376px 1fr;
    column-gap: 40px;
    display: grid;
  }

  .wrapper__sidebar {
    grid-template-rows: 472px 490px;
    grid-template-columns: 1fr;
    row-gap: 86px;
  }
}

