.wrapper {
    height: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 72px 1fr;
    grid-template-areas: "header"
        "main";
}

.wrapper__header {
    grid-area: header;
}

.main {
    grid-area: main;

    margin-top: 40px;
}

.wrapper__sidebar {
    grid-area: sidebar;
}

.wrapper__content {
    grid-area: content;
}

@media (min-width:768px) {
    .wrapper {
        height: 100%;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 72px 1fr;
        grid-template-areas: "header"
            "main";
    }

    .main {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 472px 1fr;
        grid-template-areas: "sidebar"
            "content";
        row-gap: 100px;
    }

    .wrapper__sidebar {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 472px;
    }
}

@media (min-width:1440px) {
    .wrapper {
        height: 100%;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 72px 1fr;
        grid-template-areas: "header"
            "main";
    }

    .main {
        display: grid;
        grid-template-columns: 376px 1fr;
        grid-template-rows: 1fr;
        grid-template-areas: "sidebar content";
        column-gap: 40px;
    }

    .wrapper__sidebar {
        grid-template-columns: 1fr;
        grid-template-rows: 472px 490px;
        row-gap: 86px;
    }
}
